import Vue from 'vue'
import $ from 'jquery'

Vue.directive('focus',{
    inserted(el) {
      el.focus()
    }
})
Vue.directive('faA',{
    inserted(el){
        console.log($(el)[0])
        el.onclick=function(){
            el.style.width='2rem';
            el.style.height='2rem';
            el.style.border=' 1px solid red'
        }
    }
})


